.cities {
  margin-bottom: 100px;
}

.cityTitle {
  letter-spacing: 1.3px;
  margin-bottom: 5px;
}

.cityDesc {
  letter-spacing: 1.3px;
  margin-bottom: 10px;
}

.cityImages {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.cityImage .cityImg {
  width: 250px;
  height: 180px;
  margin-bottom: 10px;
}

.cityImage .cityImg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
  transition: all 0.5s;
}

.cityImgTitle {
  letter-spacing: 1.3px;
  text-transform: uppercase;
  color: #ff7f50;
  text-decoration: none;
}

.cityImgPractitioners {
  font-size: 14px;
  letter-spacing: 1.3px;
}

.cityImage:hover .cityImg img {
  transform: scale(1.02);
}